/* Font Importing*/

@font-face {
    font-family: 'Frijole';
    src: url('../../assets/fonts/Frijole.ttf')
    format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Press Start 2P';
    src: url('../../assets/fonts/PressStart2P.ttf')
    format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Nunito';
    src: url('../../assets/fonts/Nunito.ttf')
    format('truetype');
    font-weight: normal;
    font-style: normal;
}

/* Body general style*/

body,
html {
    background-color: #111111;
    color: white;
}

* {
    box-sizing: border-box;
}

/* page header*/

header {
    display: flex;
    position: absolute;
    height: 15%;
    min-height: 75px;
    max-height: 125px;
    width: 95%;
    max-width: 2000px;
    min-width: 250px; 
    background-color: #1b1b1b;
    box-shadow: 0.5rem 0.5rem 0.5rem #0a0a0a;
    color: #ffffff;
    left: 50%;
    top: 0;
    transform: translate(-50%,0%);
    z-index: 2;
}

nav.upheader {
    height: 75%;
    width: 100%;
    display: inline-flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: space-around;
    justify-content: space-between;
    align-items: center;
}

nav.upheader span {
    display: inline-flex;
}

a.logo {
    font-family: 'Frijole';
    color: #69d300;
    font-size: 25px;
    margin-left: 25px;
    transition: 1000ms;
    text-decoration: none;
}

@media only screen and (max-width: 1200px) {
    nav.upheader span {
        font-size: 20px;
    }
  }

a.login {
    font-family: 'Nunito';
    text-decoration: none;
    font-size: 20px;
    color: #ffffff;
    transition: 1000ms;
    margin-right: 10px;
}

@media only screen and (max-width: 1200px) {
    nav.upheader span a {
        font-size: 15px;
    }
}

ul {
    background-color: #333;
    list-style-type: none;
    display: inline-flex;
    overflow: hidden;
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translate(-50%,0%);
}

ul li {
    float: left;
    position: relative;
    margin-left: 15px;
    width: 16%;
    height: 30px;
    display: flex;
    transition: 400ms;
    flex-direction: column;
    justify-content: center;
}

@media only screen and (max-width: 1200px) {
    ul li {
        height: 20px;
    }
}

ul li:hover {
    cursor: pointer;
    background-color: rgb(36, 35, 35);
}

ul li a {
    display: block;
    padding: 8px;
    text-align: center;
    text-decoration: none;
    color: #ffffff;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif ;
    transition: 1000ms;
}

@media only screen and (max-width: 1200px) {
    ul li a {
        font-size: 12px;
    }
}

@media only screen and (max-width: 600px) {
    ul li a {
        font-size: 8px;
    }
}

/*Main part of the page*/

main {
    background-color: #111111;  
    height: 1500px;
    min-height: 750px;
    max-width: 2000px;
    min-width: 200px; 
    width: 95%;
    margin-top: 100px; 
    position: absolute; 
    z-index: 1;
    left: 50%;
    top: 5%;
    transform: translate(-50%,0%);
    transition: 1000ms;
}

@media only screen and (max-width: 1200px) {
    main {
        margin-top: 85px;
    }
  }